body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
}

ol, ul {
  padding-left: 30px;
}

.hidden {
  display: none;
}

.board-row {
  display: flex;
  width: 100%;
  justify-content: space-between;
}

.card {
  height: 300px;
}

.card img {
  max-width: 100%;
  max-height: 100%;
  border-radius: 20px;
}

.game-field {
  width: 200px;
  margin-right: 30px;
}


.status {
  margin-bottom: 10px;
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 34px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 34px;
}

.square:focus {
  outline: none;
}

.token-btn {
  width: 60px;
  height: 60px;
  padding: 0;
  border-radius: 10px;
  border: none;
  color: white;
  font-weight: 700;
  align-items: center;
  vertical-align: middle;
  position: relative;
  background-color: transparent;
}

.token-btn img {
  width: 60px;
  height: 60px;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  flex-direction: row;
  border: 3px solid teal;
  border-radius: 15px;
  padding: 30px;
  background-color: #74bdbd; 
  width: 525px;
}


.overlay{
  position: absolute;
  width: 0%;
  height: 0%;
  top: 0%;
  z-index: 100;
  display: none;
}
.overlay img {
  opacity: 0.8;
  position: relative;
}

.result {
  width: 0%;
  height: 100%;
}

.active-overlay{
  top: 0;
  position: absolute;
  z-index: 100;
  display: block;
}

.answer {
  width: 100px;
  height: 150px;
  margin-right: 30px;
  border-radius: 10px;
  background-image: url('../src/assets/images/reverse.png');
  background-size: cover;
  color: black
}

.game-board {
  display: flex;
  width: 100%;
}
.game-info {
  font-weight: 700;
  font-family: fantasy;
}

#score {
  height: 40px;
  color: #014848;
}

#answer {
  height: 25px;
  color: #014848;
}
